.header {
  display: flex;
  padding: 15px 20px;
  justify-content: space-between;
  position: sticky;
  background-color: white;
  z-index: 100;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.header__left {
  display: flex;
  justify-content: space-evenly;
}

.headerLeft__input {
  display: flex;
  align-items: center;
  background-color: #eff2f5;
  border-radius: 999px;
  padding: 10px;
  margin-left: 10px;
}

.headerLeft__input > input {
  border: none;
  /* border-color: rgb(50, 139, 255); */
  padding: 0 10px;
  background: transparent;
  outline-width: 0;
}
.headerLeft__imageLogo {
  height: 40px;
}
.header__center {
  display: flex;
  flex: 1;
  justify-content: center;
}

.headerCenter__option > .MuiSvgIcon-root {
  color: gray;
}

.headerCenter__option:hover > .MuiSvgIcon-root {
  color: #2e81f4;
}

.headerCenter__option {
  display: flex;
  align-items: center;
  padding: 0 30px;
  cursor: pointer;
}

.headerCenter__option--active > .MuiSvgIcon-root {
  color: #2e81f4;
}

.headerCenter__option--active {
  border-bottom: 4px solid #2e81f4;
}
.headerCenter__option:hover {
  background-color: #eff2f5;
  border-radius: 10px;
  align-items: center;
  padding: 0 30px;
  border-bottom: none;
}

.header__right {
  display: flex;
  align-items: center;
}
.headerRight__info {
  display: flex;
  align-items: center;
}
.headerRight__info > h4 {
  margin-left: 10px;
}
